function initialSize() {
  if(window.innerWidth<1200){
    change=document.querySelector("#stick600")
  }
  if(window.innerWidth<=600){
    change=document.querySelector("#stick300")
  }
  if(window.innerWidth>=1200){
    change=document.querySelector("#stick")
  }
}

function pullInitial(){
    change.querySelector('#eleven_comb').style.opacity=1;
    if(window.innerWidth>=1250){
      change.querySelector('#eleven').style.opacity=1;
      change.querySelector('#twelve').style.opacity=0;
      change.querySelector('#thirteen').style.opacity=0;
      change.querySelector('#fourteen').style.opacity=0;
      change.querySelector('#fifteen').style.opacity=0;
      change.querySelector('#sixteen').style.opacity=0;
      change.querySelector('#seventeen').style.opacity=0;
      change.querySelector('#eighteen').style.opacity=0;
      change.querySelector('#nineteen').style.opacity=0;
      change.querySelector('#twenty').style.opacity=0;
      change.querySelector('#twenty-one').style.opacity=0;
    }
}
