function init() {
  initialSize();
  pullInitial();
  scroller
    .setup({
      step: "#scrolly article .step",
      offset: 0.5 ,
      debug: false
    })
    .onStepEnter(handleStepEnter);

  window.addEventListener("resize", scroller.resize);
  //window.addEventListener("resize", resizeSVG);
}

// kick things off
init();